.container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 480px;

  @media screen and (min-width: 480px) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 768px) {
    max-width: 658px;
  }

  @media screen and (min-width: 1024px) {
    max-width: 922px;
  }
}
